<template>
  <el-dialog
    title="合同核验"
    :visible.sync="visible"
    :close-on-click-modal="false"
    @close="handleClose"
  >
    <div class="box" v-loading="loading">
      <div class="file-box">
        <div>
          <el-upload
            ref="upload"
            v-show="!file.fid"
            class="upload"
            :action="uploadUrl"
            :limit="1"
            :multiple="false"
            drag
            list-type="picture-card"
            :headers="headers"
            accept="doc, docx"
            :on-success="handleUpload"
            :before-upload="handleBeforeUpload"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <div class="preview" v-show="file.fid">
            <img class="upload-thumbnail" :src="file.previewUrl" />
            <span class="btn zoom" @click="handlePreview">
              <i class="el-icon-zoom-in"></i>
            </span>
            <span class="btn remove" @click="handleRemove">
              <i class="el-icon-delete"></i>
            </span>
          </div>
        </div>
        <div class="file-name" v-show="!!file.fid">{{file.name}}</div>
        <div class="file-tip" v-show="!file.fid">选择上传一份合同，立刻核验签名是否有效，内容是否被篡改</div>
      </div>
      <div class="result-box" v-show="!!file.fid">
        <p>
          <span class="title">合同名称</span>
          {{verifyResult.contractName}}
        </p>
        <p>
          <span class="title">签名信息</span>
          {{verifyResult.hasSignature ? "有效" : "无效"}}
          <i
            v-if="verifyResult.hasSignature"
            style="color:#67C23A"
            class="el-icon-success"
          ></i>
          <i v-else style="color:red" class="el-icon-close"></i>
        </p>
        <p>
          <span class="title">文档内容</span>
          {{verifyResult.summary}}
          <i
            v-if="verifyResult.isSafe"
            style="color:#67C23A"
            class="el-icon-success"
          ></i>
          <i v-else style="color:red" class="el-icon-close"></i>
        </p>
        <p>
          <span class="title">负责人</span>
          {{verifyResult.modifierName}}
        </p>
        <p>
          <span class="title">借阅人</span>
          {{verifyResult.loaderName}}
        </p>
        <p>
          <span class="title">借阅日期</span>
          {{verifyResult.loadTime}}
        </p>
        <p>
          <span class="title">版本</span>
          {{verifyResult.version}}
        </p>
        <p>
          <span class="title">所属项目</span>
          {{verifyResult.projectName}}
        </p>
      </div>
    </div>
    <el-dialog :visible.sync="dialogVisible" append-to-body>
      <div class="box">
        <img class="preview-page" :src="file.previewUrl" />
      </div>
    </el-dialog>
  </el-dialog>
</template>

<script>
import { uploadTempContract, verifySignature } from '@/action/contract'
import { filePreview } from '@/action/open'
import { parseTime } from '@/utils'
export default {
  props: ['open', 'cid'],
  data() {
    return {
      uploadUrl: uploadTempContract(),
      headers: {
        Authorization: this.$store.getters.token,
      },
      loading: false,
      visible: this.open,
      dialogVisible: false,
      disabled: false,
      file: {
        fid: '',
        uri: '',
        name: '',
        previewUrl: '',
        verifyResult: {},
        origin: {},
      },
      signature: {},
    }
  },
  computed: {
    verifyResult() {
      const {
        cid = "00000000",
        contractName = "未知",
        loadTime,
        loaderId,
        loaderName = "未知",
        modifierId,
        modifierName = "未知",
        pid,
        projectName = "未知",
        summary,
      } = this.signature

      return {
        contractName,
        hasSignature: !!this.signature,
        isSafe: !(summary === ''),
        summary: summary === '' ? '文件内容已被更改！' : '文件内容未被更改',
        loaderName,
        loadTime: parseTime(loadTime),
        modifierName,
        projectName,
        version:
          cid === this.cid
            ? '版本一致'
            : '版本不一致：当前文件版本号为 ' + cid.slice(-8),
      }
    },
  },
  watch: {
    open(newV, oldV) {
      this.visible = newV
    },
  },
  methods: {
    handleBeforeUpload() {
      this.loading = true
    },
    async handleUpload(file) {
      let { name, type } = file.baseInfo
      this.file = {
        fid: file.cid,
        uri: file.uri,
        name: name + '.' + type,
        previewUrl: '',
        origin: file,
      }
      this.file.previewUrl = await filePreview(file.cid, name, type, file.uri)
      this.signature = await verifySignature(file.cid)
      this.loading = false
    },
    handleRemove() {
      this.$refs.upload.clearFiles()
      this.$nextTick((_) => {
        this.clear()
      })
    },
    handlePreview() {
      this.dialogVisible = true
    },
    handleClose() {
      this.clear()
      this.$emit('close')
    },
    clear() {
      this.file = {
        fid: '',
        uri: '',
        name: '',
        previewUrl: '',
        origin: {},
      }
    },
  },
}
</script>

<style scoped>
.box {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.file-box {
  width: 270px;
  text-align: center;
}

.file-tip {
  font-size: 12px;
  padding: 30px;
  color: #909399;
}

.upload >>> .el-upload {
  width: 180px;
  height: 260px;
  line-height: 280px;
}

.upload >>> .el-upload-dragger {
  width: 180px;
  height: 260px;
  line-height: 280px;
  z-index: 999;
}

.preview {
  position: relative;
  width: 180px;
  height: 260px;
  line-height: 280px;
  margin: auto;
}

.preview > .btn {
  display: none;
}

.preview:hover > .btn {
  display: block;
}

.preview > img {
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
}

.preview > .btn {
  position: absolute;
  width: 50%;
  height: 100%;
  font-size: 24px;
  color: white;
  font-weight: 900;
  background-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.preview .zoom {
  left: 0;
}

.preview .remove {
  left: 50%;
}

.preview-page {
  height: 100%;
  margin: auto;
}

.file-name {
  padding: 30px;
}

.result-box {
  flex: 1;
  padding-bottom: 50px;
}

.result-box > p {
  color: #757575;
  margin: 30px;
}

.title {
  display: inline-block;
  height: 100%;
  width: 120px;
  color: #303133;
}
</style>



