import Vue from "vue";
import * as Server from "@/action/examine";

const state = {
  creator: [],
  director: [],
  drafter: [],
  auditor: [],
  projectMap: {},
};

const mutations = {
  init(state, projects) {
    state.creator = projects.creator;
    state.director = projects.director;
    state.drafter = projects.drafter;
    state.auditor = projects.auditor;
    [
      ...projects.creator,
      ...projects.director,
      ...projects.drafter,
      ...projects.auditor
    ].forEach(e => {
      Vue.set(state.projectMap, e.pid, e)
    });
  },
  clear(state) {
    state.creator = [];
    state.director = [];
    state.drafter = [];
    state.auditor = [];
    state.projectMap = {};
  }
};

const actions = {
  async getProjects({ commit }) {
    let projects = await Server.listMyProject();
    commit("clear");
    commit("init", projects);
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};



