<template>
  <div>
    <div class="page-up" @click="scrollToTop" v-show="toTopShow">
      <span class="icon scrollTop">
        <img src="./top.svg" alt="">
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'to-top',
  data() {
    return {
      toTopShow: false,
      target: {}
    }
  },
  mounted() {
    this.$nextTick(function() {
      window.addEventListener('scroll', this.handleScroll, true)
    })
  },
  destroyed() {
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll(e) {
      let dom = e.target.scrollingElement
      this.target = dom
      this.scrollTop = dom.scrollTop
      if (this.scrollTop > 300) {
        this.toTopShow = true
      } else {
        this.toTopShow = false
      }
    },
    scrollToTop() {
      let timer = null
      let _this = this
      cancelAnimationFrame(timer)
      timer = requestAnimationFrame(function fn() {
        if (_this.scrollTop > 5000) {
          _this.scrollTop -= 1000
          _this.target.scrollTop = _this.scrollTop
          timer = requestAnimationFrame(fn)
        } else if (_this.scrollTop > 1000 && _this.scrollTop <= 5000) {
          _this.scrollTop -= 500
          _this.target.scrollTop = _this.scrollTop
          timer = requestAnimationFrame(fn)
        } else if (_this.scrollTop > 200 && _this.scrollTop <= 1000) {
          _this.scrollTop -= 100
          _this.target.scrollTop = _this.scrollTop
          timer = requestAnimationFrame(fn)
        } else if (_this.scrollTop > 50 && _this.scrollTop <= 200) {
          _this.scrollTop -= 10
          _this.target.scrollTop = _this.scrollTop
          timer = requestAnimationFrame(fn)
        } else if (_this.scrollTop > 0 && _this.scrollTop <= 50) {
          _this.scrollTop -= 5
          _this.target.scrollTop = _this.scrollTop
          timer = requestAnimationFrame(fn)
        } else {
          cancelAnimationFrame(timer)
          _this.toTopShow = false
        }
      })
    }
  }
}
</script>

<style scoped>
.page-up {
  background-color: #f7f7f7;
  position: fixed;
  right: 30px;
  bottom: 55px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.5);
  opacity: .8;
  z-index: 100;
}
.scrollTop {
  font-size: 40px;
  z-index: 10000;
  color: black;
}
.icon img{
  width: 40px;
  height: 40px;
}
</style>



