<template>
  <div class="draft-box">
    <word-item v-for="item in contracts" :key="item.cid" :contract="item" mode="draft" />
  </div>
</template>

<script>
import WordItem from '@/components/WordItem'
export default {
  name: 'draft',
  components: { WordItem },
  computed: {
    contracts() {
      let contracts = this.$store.state.contracts.dataList
      return contracts.filter(e => e.status == 2)
    }
  }
}
</script>

<style scoped>
.draft-box {
  position: relative;
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  overflow: visible;
}
</style>



