<template>
  <div class="draft-box">
    <word-item v-for="item in contracts" :key="item.cid" :contract="item" mode="draft" />
  </div>
</template>

<script>
import WordItem from '@/components/WordItem'
export default {
  name: 'draft',
  components: { WordItem },
  computed: {
    contracts() {
      let pid = this.$route.query.pid
      let dept = this.$store.state.projects.projectMap[pid]

      let contracts = this.$store.state.contracts.dataList
      let myDept = this.$store.getters.myDept
      return contracts
        .filter(e => e.status == 0)
        .filter(
          e => myDept.indexOf(e.handler) != -1 || myDept[0] == dept.director.uid
        )
    }
  }
}
</script>

<style scoped>
.draft-box {
  position: relative;
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  overflow: visible;
}
</style>



