import request from "./request";

/**
 * 通过验证码注册
 */
export function registByPhone(data) {
  return request({
    method: "POST",
    url: `/users/?code=${data.captcha}`,
    data
  });
}

/**
 * 通过token登录
 */
export function loginByToken(data) {
  return request({
    method: "GET",
    url: "/users/login/"
  });
}

/**
 * 通过密码登录
 */
export function loginByPw(data) {
  return request({
    method: "PUT",
    url: "/users/login/pw",
    data
  });
}

/**
 * 获取用户信息
 */
export function getUserInfo(data) {
  return request({
    method: "GET",
    url: `/users/${data}`
  });
}

/**
 * 获取所有用户信息
 */
export function getUsers() {
  return request({
    method: "GET",
    url: `/users`
  });
}

/**
 * 获取头像地址
 */
export function getAvatarUri(uid) {
  return `http://legal.tgozzz.cn/users/${uid}/portrait`;
}


/**
 * 设置默认签名
 */
export function setDefaultSign(uid, index) {
  return request({
    method: "PUT",
    url: `/users/${uid}/signs/${index}`
  });
}

/**
 * 删除某条签名
 */
export function deleteSign(uid, index) {
  return request({
    method: "DELETE",
    url: `/users/${uid}/signs/${index}`
  });
}

/**
 * 修改用户基本信息
 */
export function reviseBase(data) {
  return request({
    method: "PUT",
    url: `/users/${data.uid}`,
    data
  });
}

/**
 * 退出登录
 */
export function exitLogin() {
  return request({
    method: "DELETE",
    url: `/users/login/`
  });
}

export function getUserSign(uid) {
  return request({
    method: 'GET',
    url: `/users/${uid}/signs/0`
  })
}



