<template>
  <div class="mine">
    <div class="title-line">
      <span class="commend-tem">我的模板</span>
    </div>
    <el-table
      :data="mineList"
      stripe
      @row-click="handlePreview"
    >
      <el-table-column
        prop="id"
        label="编号"
        min-width="10%"
      ></el-table-column>
      <el-table-column
        prop="name"
        label="模板名称"
        min-width="45%"
      ></el-table-column>
      <el-table-column
        prop="lib"
        label="所属模板库"
        min-width="15%"
      ></el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间"
        min-width="15%"
      ></el-table-column>
      <el-table-column
        prop="process"
        label="操作"
        min-width="15%"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >移除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      small
      layout="prev, pager, next"
      class="page-line"
      :current-page="currpage"
      :page-size="pagesize"
      :total="mineInfo.length"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
import * as Service from "@/action/user";
import * as TemplateService from "@/action/template";
import { formatTime } from "@/utils";
export default {
  name: "mine",
  created() {
    this.getUserMine();
  },
  mounted() {
    this.pagesize = this.calculatePagesize();
  },
  computed: {
    mineList() {
      let size = this.pagesize;
      let curr = this.currpage;
      return this.mineInfo.filter(
        (v, index) => size * curr - size <= index && index < size * curr
      );
    }
  },
  methods: {
    //  初始化收藏数据
    async getUserMine() {
      let mineTids = this.$store.state.user.template.mine || [];
      let mineInfo = [];
      if (mineTids) {
        try {
          let templatesInfo = await TemplateService.getTemplatesByIds(
            mineTids
          );
          if (templatesInfo) {
            templatesInfo.forEach((value, index) => {
              let libName = "";
              this.$store.state.user.libs.forEach(v => {
                if (v.tgid == value.group) {
                  libName = v.category;
                }
              });
              mineInfo.push({
                id: ++index,
                name: value.name,
                lib: libName,
                createTime: formatTime(value.createTime),
                tid: value.tid,
                group: value.group
              });
            });
            this.mineInfo = mineInfo;
          }
        } catch (error) {
          console.log(error.response);
        }
      }
    },
    // 计算每页最合适的数量
    calculatePagesize() {
      let headH = 60,
        unitH = 48,
        bottomH = 46;
      let totalH = document.getElementsByClassName("work-content")[0]
        .clientHeight;
      return Math.floor((totalH - headH - bottomH) / unitH - 2);
    },
    // 页数改变
    currentChange(arg) {
      this.currpage = arg;
    },
    // 回到第一页
    resetPagination() {
      this.currpage = 1;
    },
    // 取消收藏
    async handleDelete(index, row) {
      try {
        this.mineInfo.splice(index, 1);
        let tid = row.tid;
        await TemplateService.removeFromFavorites(this.$store.state.user.uid, tid);
        this.$store.commit("user/cancelMarkTemp", tid);
        this.$message({
          message: '取消收藏成功',
          type: 'success'
        });
      } catch (error) {
        console.log(error.response);
        this.$message({
          message: '取消收藏失敗',
          type: 'error'
        });
      }
    },
    handlePreview(row) {
      this.$router.push({
        path: '/office',
        query: {
          mode: 'view',
          template: row.tid,
          group: row.group
        }
      })
    }
  },
  data() {
    return {
      mineInfo: [],
      pagesize: 0,
      currpage: 1
    };
  }
};
</script>

<style scoped>
.mine {
  width: 98%;
  height: 100%;
  margin-left: 2%;
  position: relative;
}
.commend-tem {
  font-size: 24px;
}
.title-line {
  display: flex;
  align-items: center;
  height: 60px;
  line-height: 60px;
  margin-bottom: 0px;
}
.page-line {
  text-align: center;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 20px;
  margin: 0 auto;
}
</style>



