import * as Service from "@/action/user";
import * as TemplateService from "@/action/template";
const state = {
  uid: "",
  phone: "",
  age: 99,
  sex: true,
  name: "",
  email: "",
  password: "******",
  portrait: "",
  token: "",
  createTime: 0,
  template: {
    mark: [],
    mine: [],
    use: []
  },
  organization: {
    status: 1,
    permission: {
      label: "微泛",
      webRoute: "*",
      httpRoute: "http:*",
      on: false,
      children: {
        management: {
          label: "管理",
          webRoute: "/",
          httpRoute: "/management",
          on: false,
          children: null
        },
        user: {
          label: "用户",
          webRoute: "/user",
          httpRoute: "/user",
          on: false,
          children: {
            template: {
              label: "查阅模板",
              webRoute: "/template",
              httpRoute: "/template",
              on: false,
              children: null
            }
          }
        }
      }
    },
    roles: [],
    department: {
      did: "",
      name: "",
      leader: false
    }
  },
  signs: [],
  notice: []
};

const mutations = {
  init(state, user) {
    Object.assign(state, user);
    this.dispatch("department/setDepartments");
    this.dispatch("projects/getProjects");
    this.dispatch("user/listTemplateLibs");
  },
  refresh(state, user) {
    Object.assign(state, user);
  },
  clearToken(state) {
    state.token = "";
    localStorage.removeItem("token");
  },
  saveToken(state, token) {
    localStorage.setItem("token", token);
  },
  markTemp(state, tid) {
    let mark = state.template.mark;
    mark.push(tid);
  },
  cancelMarkTemp(state, tid) {
    let mark = state.template.mark;
    for (let i = 0; i < mark.length; i++) {
      const e = array[i];
      if (e == tid) return mark.splice(i, 1);
    }
  },
  setLibs(state, libs) {
    state.libs = libs;
  },
  updateSigns(state, signs) {
    state.signs = signs;
  },
  updateBase(state, baseInfo) {
    state.name = baseInfo.name;
    state.age = parseInt(baseInfo.age);
    state.sex = baseInfo.sex
    state.email = baseInfo.email;
  },
  exitLogin(state) {
    let user = {
      uid: "",
      phone: "",
      age: 99,
      sex: true,
      name: "",
      email: "",
      password: "******",
      portrait: "",
      token: "",
      createTime: 0,
      template: {
        mark: [],
        mine: [],
        use: []
      },
      organization: {
        status: 1,
        permission: {
          label: "微泛",
          webRoute: "*",
          httpRoute: "http:*",
          on: false,
          children: {
            management: {
              label: "管理",
              webRoute: "/",
              httpRoute: "/management",
              on: false,
              children: null
            },
            user: {
              label: "用户",
              webRoute: "/user",
              httpRoute: "/user",
              on: false,
              children: {
                template: {
                  label: "查阅模板",
                  webRoute: "/template",
                  httpRoute: "/template",
                  on: false,
                  children: null
                }
              }
            }
          }
        },
        roles: [],
        department: {
          did: "",
          name: "",
          leader: false
        }
      },
      signs: [],
      notice: []
    };
    Object.assign(state, user);
  }
};

const actions = {
  async registWithPhone({ commit }, form) {
    let user = await Service.registByPhone(form);
    commit("init", user);
    commit("saveToken", user.token);
  },
  async loginWithToken({ commit }) {
    try {
      let user = await Service.loginByToken();
      commit("init", user);
      return true;
    } catch (err) {
      return false;
    }
  },
  async loginWithPw({ commit }, form) {
    let user = await Service.loginByPw(form);
    commit("init", user);
    commit("saveToken", user.token);
  },
  async listTemplateLibs({ commit }) {
    try {
      let libs = await TemplateService.listTemplateLibs();
      commit("setLibs", libs);
    } catch (error) {
      console.log(error);
    }
  },
  updateSigns({ commit }, uid) {
    return setInterval(async () => {
      try {
        let userInfo = await Service.getUserInfo(uid);
        commit("updateSigns", userInfo.signs);
      } catch (error) {
        console.log(error);
      }
    }, 1000);
  },
  updateBase({ commit }, baseInfo) {
    console.log(baseInfo);
    commit("updateBase", baseInfo);
  },
  exitLogin({ commit }) {
    commit("exitLogin");
  },
  async refreshUserInfo({ commit }) {
    let user = await Service.loginByToken();
    commit("refresh", user);
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};



