const getters = {
  token: state => state.user.token,
  hasToken: () => !!localStorage.getItem("token"),
  hasLogin: state => !!state.user.uid,
  projectNavList: state => {
    let map = state.projects.projectMap;
    let res = [];

    for (const key in map)
      res.push({
        key,
        name: map[key].baseInfo.name,
        time: map[key].createInfo.time
      });
    res.sort((e1, e2) => e1.time - e2.time);

    return res;
  },
  myDept: state => {
    let res = [];
    let user = state.user;
    let deptMap = state.department.deptMap;

    if (!user.uid || !deptMap) return [];

    let did = user.organization.department.did;
    res.push(user.uid);
    while (did) {
      res.push(did);
      did = deptMap[did] ? deptMap[did].superior : false;
    }

    return res;
  }
};

export default getters;



