<template>
  <div>
    <div class="title-line">项目 / {{project.baseInfo.name}} / {{contract.baseInfo.name}}</div>
    <el-timeline reverse>
      <el-timeline-item
        v-for="(item,index) in contract.histories"
        :key="item.cid + index"
        :timestamp="timeFormater(item.time)"
        placement="top"
      >
        <card :history-item="item" :pid="project.pid" :cid="contract.cid" />
      </el-timeline-item>
    </el-timeline>
  </div>
</template>

<script>
import Card from './card'
import { parseTime } from '@/utils'
export default {
  name: 'history',
  components: { Card },
  data() {
    return {}
  },
  computed: {
    project() {
      return (
        this.$store.state.projects.projectMap[this.$route.query.pid] || {
          baseInfo: {
            name: '未知'
          }
        }
      )
    },
    contract() {
      return (
        this.$store.state.contracts.contractMap[this.$route.query.cid] || {
          baseInfo: { name: '未知' },
          histories: []
        }
      )
    }
  },
  methods: {
    timeFormater(t) {
      if (!t) t = Date.now()
      return parseTime(t, '{y}/{m}/{d}')
    }
  }
}
</script>

<style scoped>
.title-line {
  font-size: 24px;
  height: 60px;
  line-height: 60px;
  margin: 0 20px 20px;
}
</style>



