<template>
  <el-card class="card">
    <h4>{{historyItem.info}}</h4>
    <p>{{historyItem.modifyAdvice}}</p>
    <p class="tip">
      <el-avatar
        shape="square"
        style="margin-right:10px"
        :size="24"
        :src="getAvatarUri(historyItem.modifierUid)"
      />
      {{historyItem.modifierName}} 提交于 {{time}}
    </p>
    <div class="operation">
      <el-button size="mini" icon="el-icon-monitor" plain @click="handlePreview">预览</el-button>
      <el-button size="mini" icon="el-icon-s-check" plain @click="handleOpenVerify">核验</el-button>
      <el-button size="mini" icon="el-icon-magic-stick" plain @click="handleRollback">
        回退
        | {{historyItem.cid.slice(-8)}}
      </el-button>
    </div>
    <img class="sign" :src=" sign ? sign : '/static/sign.png'" alt="sign" />
    <security-box :open="open" :cid="cid" @close="handleCloseVerify" />
  </el-card>
</template>

<script>
import { formatTime } from '@/utils'
import { getAvatarUri, getUserSign } from '@/action/user'
import { rollbackContract } from '@/action/contract'
import SecurityBox from '@/components/SecurityBox'
export default {
  name: 'card',
  components: { SecurityBox },
  props: ['history-item', 'pid', 'cid'],
  data() {
    return {
      sign: '',
      open: false,
    }
  },
  computed: {
    time() {
      return formatTime(this.$props.historyItem.time)
    },
  },
  created() {
    this.getUserSign()
  },
  methods: {
    async getUserSign() {
      this.sign = await getUserSign(this.historyItem.modifierUid)
    },
    getAvatarUri(uid) {
      return getAvatarUri(uid)
    },
    handleOpenVerify() {
      this.open = true
    },
    handleCloseVerify() {
      this.open = false
    },
    handlePreview() {},
    handleRollback() {
      this.$confirm('此操作将回滚版本，且不可逆转，是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(async () => {
          await rollbackContract(this.pid, {
            rollback2: this.historyItem.cid,
            lastCid: this.cid,
          })
          await this.$store.dispatch('contracts/getListByPid', this.pid)
          this.$message({
            type: 'success',
            message: '回滚成功!',
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消回滚',
          })
        })
    },
  },
}
</script>

<style scoped>
.card {
  position: relative;
}
.sign {
  position: absolute;
  right: 20px;
  bottom: 10px;
  width: 200px;
  height: 64px;
  border: 2px dotted #dcdfe6;
  padding: 2px;
}
.operation {
  position: absolute;
  top: 10px;
  right: 20px;
}
p {
  word-break: break-all;
  max-width: calc(95% - 250px);
}
.tip {
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #909399;
}
</style>



