import Vue from "vue";
import * as Service from "@/action/organization";
const state = {
  deptList: [],
  deptMap: {}
};

const mutations = {
  init(state, departments) {
    state.deptList.splice(0, state.length);
    state.deptList.push(...departments);
    departments.forEach(e => {
      Vue.set(state.deptMap, e.did, e);
    });
  }
};

const actions = {
  async setDepartments({ commit }) {
    try {
      let data = await Service.getDepartment();
      commit("init", data);
    } catch (error) {
      console.log(error);
    }
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};



