import Examine from "@/pages/examine";
import MyProject from "@/pages/examine/myproject";
import WriteInfo from "@/pages/examine/myproject/components/writeInfo";
import SelectDepartment from "@/pages/examine/myproject/components/selectDepartment";
import OrderDepartment from "@/pages/examine/myproject/components/orderDepartment";
import Signature from "@/pages/examine/signature";

import Projects from "@/pages/examine/projects";
import Upload from "@/pages/examine/upload";
import History from "@/pages/examine/history"

import TemplateLib from "@/pages/examine/templateLib";
import FavoriteTemplate from "@/pages/examine/favoriteTemplate";
import MyTemplate from "@/pages/examine/myTemplate";
import AddTemplate from "@/pages/examine/templateCommon/AddTemplate";
import AddTemplateLib from "@/pages/examine/templateCommon/AddTemplateLib";

export default {
  path: "/examine",
  name: "IndexExamine",
  redirect: "/examine/myproject",
  component: Examine,
  children: [
    { path: "myproject", name: "myProject", component: MyProject },
    {
      path: "writeinfo",
      name: "writeInfo",
      component: WriteInfo
    },
    {
      path: "selectdepartment",
      name: "selectDepartment",
      component: SelectDepartment
    },
    {
      path: "orderdepartment",
      name: "orderDepartment",
      component: OrderDepartment
    },
    {
      path: "signature",
      name: "signature",
      component: Signature
    },
    {
      path: "projects",
      name: "projects",
      component: Projects
    },
    {
      path: "upload",
      name: "upload",
      component: Upload
    },
    {
      path: "history",
      name: "history",
      component: History
    },
    {
      path: "templatelib",
      name: "templateLib",
      component: TemplateLib
    },
    {
      path: "favoritetemplate",
      name: "favoriteTemplate",
      component: FavoriteTemplate
    },
    {
      path: "mytemplate",
      name: "myTemplate",
      component: MyTemplate
    },
    {
      path: "addTemplate",
      name: "addTemplate",
      component: AddTemplate
    },
    {
      path: "addTemplateLib",
      name: "addTemplateLib",
      component: AddTemplateLib
    }
  ]
};



