<template>
  <div>
    <div class="title-text">我的部门</div>
    <div class="info-line">
      <div class="head-box">
        <img class="head-img" :src="$store.state.user.portrait" alt />
      </div>
      <div class="user-info">
        <div class="user-name">{{$store.state.user.name}}</div>
        <div>
          <!-- <span
            v-for="(value,index) of myDept"
            :key="index"
          >{{ deptMap[value] ? deptMap[value].name: ""}}</span> -->
          <span class="user-department">{{getDeptNamePath()}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PersonalDepartment",
  computed: {
    myDept() {
      return this.$store.getters.myDept;
    },
    deptMap() {
      return this.$store.state.department.deptMap;
    }
  },
  methods: {
    getDeptNamePath() {
      let did = this.myDept[1];
      let map = this.$store.state.department.deptMap;
      let path = [];
      while (map[did]) {
        path.unshift(map[did].name);
        did = map[did].superior;
      }
      return path.join(" / ");
    }
  }
};
</script>

<style scoped>
.title-text {
  font-size: 18px;
  margin-bottom: 17px;
}
.info-line {
  height: 200px;
  background: coral;
  display: flex;
  align-items: center;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
   box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}
.user-info {
  padding: 20px;
  padding-left: 50px;
  box-sizing: border-box;
}
.user-name {
  margin-bottom: 20px;
  font-size: 30px;
  /* font-weight: bold; */
  color: #333;
}
.user-department {
  font-size: 20px;
  /* font-weight: 100; */
  color: #444;
}
.head-img {
  height: 100%;
}
.head-box {
  height: 100%;
}
</style>


