<template>
  <div class="draft-box">
    <word-item v-for="item in contracts" :key="item.cid" :contract="item" mode="view" />
  </div>
</template>

<script>
import WordItem from '@/components/WordItem'
export default {
  name: 'draft',
  components: { WordItem },
  computed: {
    contracts() {
      let contracts = this.$store.state.contracts.dataList
      let myDept = this.$store.getters.myDept
      return contracts
        .filter(e => e.status != 2)
        .filter(e => myDept.indexOf(e.handler) == -1)
    }
  }
}
</script>

<style scoped>
.draft-box {
  position: relative;
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  overflow: visible;
}
</style>



