<template>
  <div class="container">
    <div class="in-container">
      <div class="title-text">创建项目</div>

      <div class="show-content">
        <step class="step-item" :activeStep="3"></step>
        <div>
          <div class="in-title-text">
            <ul>
              <li>设定审核流程</li>
            </ul>
          </div>
          <div class="drag">
            <draggable class="list-group" :list="departments" group="departments">
              <div
                class="list-group-item"
                v-for="element in departments"
                :key="element.name"
              >{{ element.name }}</div>
            </draggable>
          </div>
          <div class="but-box">
            <el-button class="but">取消</el-button>
            <el-button class="but" type="primary" @click="addProject">确认</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Step from './step'
import draggable from 'vuedraggable'
import { getLeafDepartment } from '@/utils'
import * as Service from '@/action/examine'
import * as UserService from '@/action/user'
export default {
  name: 'orderDepartment',
  components: {
    Step,
    draggable
  },
  data() {
    return {
      departments: [],
      form: {
        baseInfo: {},
        drafter: {},
        director: {},
        auditor: []
      }
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      let startOrders = this.$route.params.info.departments;
      this.departments = getLeafDepartment(startOrders[0]);
      this.departments.forEach(value=> {
        value.name = this.getDeptNamePath(value.did)
      })
      this.initForm();
    },
    getDeptNamePath(did) {
      let map = this.$store.state.department.deptMap;
      let path = [];
      while (map[did]) {
        path.unshift(map[did].name);
        did = map[did].superior;
      }
      return path.join(" / ");
    },
    async initForm() {
      try {
        // 项目基本信息
        let baseInfo = this.$route.params.info
        // 负责人的uid
        let directorUid = this.$route.params.info.leader
        // 负责人
        let director = await UserService.getUserInfo(directorUid)
        // 拟稿部门
        let drafter = this.findDeptByDid(
          this.$store.state.user.organization.department.did
        )

        this.form.baseInfo = {
          name: baseInfo.name,
          tag: baseInfo.tags.join(' '),
          partner: baseInfo.cors.map(cor => cor.value).join('，'),
          desc: baseInfo.desc
        }

        this.form.director = {
          uid: director.uid,
          name: director.name,
          phone: director.phone
        }

        this.form.drafter = {
          did: drafter.did,
          name: drafter.name,
          phone: drafter.leaderPhone,
          responsibility: ''
        }
      } catch (error) {
        console.log(error)
      }
    },
    async addProject() {
      let auditor = this.departments

      this.form.auditor = auditor.map((dept, index) => {
        return {
          did: dept.did,
          order: ++index,
          name: dept.name,
          phone: dept.leaderPhone,
          responsibility: ''
        }
      })

      this.$nextTick(async () => {
        await Service.addProject(this.form)
        this.$message.success('创建项目成功')
        this.$router.replace({ path: '/examine/myproject' })
      })
    },
    findDeptByDid(did) {
      let res = null
      res = this.$store.state.department.deptMap[did]
      return res
    }
  }
}
</script>

<style scoped>
.in-container {
  /* overflow: auto; */
  width: 98%;
  margin-left: 3%;
  height: 100%;
}
.container {
  height: 100%;
}
.title-text {
  margin-bottom: 10px;
  font-size: 20px;
}
.in-title-text {
  margin-top: 1%;
  color: #333;
  font-size: 17px;
}
.show-content {
  background: white;
  /* overflow: scroll; */
  width: 100%;
  min-height: 75vh;
  padding-top: 20px;
  box-sizing: border-box;
}
.step-item {
  width: 85%;
  margin: 0 auto;
}
.drag {
  width: 50%;
  margin-left: 30px;
  border: #f2f2f2 solid 2px;
  background: white;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  /* box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1); */
}
.list-group-item {
  border: #aaa solid 1.2px;
  margin-bottom: 10px;
  margin-top: 10px;
  height: 30px;
  line-height: 30px;
  padding-left: 10px;
  background: white;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
.but-box {
  margin-top: 30px;
  margin-left: 20px;
  width: 50%;
  text-align: left;
}
.but {
  margin-left: 20px;
}
</style>



