<template>
  <div>
    <div class="title-line">添加合同模板</div>
    <el-form label-width="80px" class="form">
      <el-form-item label="描述">
        <el-input v-model="desc" type="textarea" :rows="2" placeholder="请输入模板描述，多模板将共享描述"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="updateInfo" type="textarea" :rows="2" placeholder="模板库更新备注"></el-input>
      </el-form-item>
      <el-form-item>
        <el-upload
          class="upload"
          ref="upload"
          multiple
          accept=".doc, .docx"
          list-type="picture"
          :on-exceed="onExceed"
          :limit="3"
          :headers="headers"
          :action="`http://legal.tgozzz.cn/templates/${group}`"
        >
          <el-button size="small">选择文件</el-button>
          <el-button size="small" @click="handleReset">重置</el-button>
          <el-button size="small" type="primary" @click="handleSubmit">提交模板</el-button>
          <div slot="tip" class="el-upload__tip">只能上传doc/docx文件</div>
        </el-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { addTemp } from '@/action/template'
export default {
  name: 'addTemplate',
  data() {
    return {
      group: '',
      headers: {
        Authorization: this.$store.getters.token
      },
      desc: '',
      updateInfo: ''
    }
  },
  created() {
    this.group = this.$route.query.group
  },
  methods: {
    onExceed() {
      this.$message.warning('不好意思，当前限制选择 3 个文件')
    },
    // 重置表单
    handleReset() {
      this.desc = ''
      this.updateInfo = ''
      this.$refs.upload.clearFiles()
    },
    // 提交表单
    async handleSubmit(e) {
      e.stopPropagation()
      let data = {
        list: this.$refs.upload.uploadFiles.map(e => ({
          tid: e.response.tid,
          info: this.desc
        })),
        info: this.updateInfo
      }
      if (!data.list || data.list.length == 0)
        return this.$message.warning('文件不可为空')
      await addTemp(data)
      await this.$confirm('上传成功，是否前往模板库查看', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '继续上传',
        type: 'success'
      })
      this.$router.push({
        path: '/template/library'
      })
    }
  }
}
</script>

<style lang="less" scoped>
.title-line {
  font-size: 24px;
  height: 60px;
  line-height: 60px;
  margin: 0 20px 20px;
}
.form {
  width: 70%;
}
.upload {
  width: 50%;
  /deep/.el-upload-list__item {
    display: inline-block;
    height: 60px;
  }
  /deep/ .el-upload-list__item-thumbnail {
    height: 40px;
    width: 40px;
    background-image: url(http://qiniu.tgozzz.cn/word.png);
    background-size: cover;
    z-index: 0;
  }
  /deep/ .el-upload-list__item-name {
    margin-top: 0;
    line-height: 40px !important;
    i {
      font-size: 40px;
    }
  }
}
</style>



