import Vue from "vue";
import Vuex from "vuex";
import user from "./user";
import getters from "./getters";
import department from "./department";
import projects from "./projects";
import contracts from "./contracts";

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    user,
    department,
    projects,
    contracts
  },
  getters
});



