<template>
  <div class="container">
    <div class="in-container">
      <div class="title-text">
        创建项目
      </div>

      <div class="show-content">
        <step
          class="step-item"
          :activeStep="2"
        ></step>
        <div class="title-text in-title-text">
          <ul>
            <li>选择相关部门</li>
          </ul>
        </div>
        <div class="select-box">
          <tree-transfer
            :title="title"
            :from_data='fromData'
            :to_data='toData'
            :defaultProps="{label:'label'}"
            @addBtn='add'
            @removeBtn='remove'
            :mode='mode'
            height='540px'
            filter
            openAll
          >
          </tree-transfer>
        </div>
        <div class="but-box">
          <!-- <router-link to="/examine/orderdepartment"> -->
          <el-button
            class="but-item"
            type="primary"
            @click="next"
          >下一步</el-button>
          <!-- </router-link> -->
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import * as Service from "@/action/examine";
import * as DepartmentService from "@/action/organization";
import treeTransfer from "el-tree-transfer";
import Step from "./step";
import { constructTree } from "@/utils";
export default {
  name: "SelectDepartment",
  components: {
    treeTransfer,
    Step
  },
  created() {
    this.getDepartment();
  },
  data() {
    return {
      title: ["公司部门", "已选择"],
      mode: "transfer", // transfer addressList
      fromData:[],
      toData: []
    };
  },
  methods: {
    // 切换模式 现有树形穿梭框模式transfer 和通讯录模式addressList

    changeMode() {
      if (this.mode == "transfer") {
        this.mode = "addressList";
      } else {
        this.mode = "transfer";
      }
    }, // 监听穿梭框组件添加

    add(fromData, toData, obj) {
      // 树形穿梭框模式transfer时，返回参数为左侧树移动后数据、右侧树移动后数据、移动的        {keys,nodes,halfKeys,halfNodes}对象

      // 通讯录模式addressList时，返回参数为右侧收件人列表、右侧抄送人列表、右侧密送人列表

      // console.log("fromData:", fromData);
      // this.fromData = fromData

      // console.log("toData:", toData);
      this.toData = toData;

      // console.log("obj:", obj);
    }, // 监听穿梭框组件移除

    remove(fromData, toData, obj) {
      // 树形穿梭框模式transfer时，返回参数为左侧树移动后数据、右侧树移动后数据、移动的{keys,nodes,halfKeys,halfNodes}对象

      // 通讯录模式addressList时，返回参数为右侧收件人列表、右侧抄送人列表、右侧密送人列表

      // console.log("fromData:", fromData);
      // this.fromData = fromData
      // console.log("toData:", toData);
      this.toData = toData;
      // console.log("obj:", obj);
    },
    async getDepartment() {
      try {
        let departmentData = await DepartmentService.getDepartment();
        console.log(departmentData);
        departmentData.forEach(value => {
          value.label = value.name;
          value.id = value.did;
          value.pid = value.superior;
        });
        let treeDepartment = constructTree(
          departmentData,
          "did",
          "superior",
          "subordinates",
          "0"
        );
        this.fromData = treeDepartment;
        console.log(treeDepartment);
      } catch (error) {
        console.log(error);
      }
    },
    next() {
      if (!this.toData.length) {
        this.$message({
          message: "选择部门不得为空",
          type: "warning"
        });
      } else {
        this.$router.push({
        name: "orderDepartment",
        params: {
          info: Object.assign(this.$route.params.info, {
            departments: this.toData
          })
        }
      });
      }
      
    }
  }
};
</script>

<style scoped>
.in-container {
  /* overflow: auto; */
  width: 98%;
  margin-left: 3%;
  height: 100%;
}
.container {
  height: 100%;
}
.title-text {
  margin-bottom: 10px;
  font-size: 20px;
}
.in-title-text {
  margin-top: 1%;
  color: #333;
  font-size: 17px;
}
.show-content {
  background: white;
  /* overflow: scroll; */
  width: 100%;
  min-height: 75vh;
  padding-top: 20px;
  box-sizing: border-box;
}
.step-item {
  width: 85%;
  margin: 0 auto;
}
.select-box {
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
}
.select-box >>> .transfer-left {
  width: 45%;
}
.select-box >>> .transfer-center {
  width: 10%;
  left: 45%;
}
.select-box >>> .transfer-right {
  width: 45%;
}
.but-box {
  margin-top: 20px;
  text-align: right;
}
.but-item {
  margin-right: 15%;
  margin-bottom: 30px;
}
</style>


