<template>
  <el-table class="table" :data="tableData" border>
    <el-table-column label="#" align="center" width="80">
      <template slot-scope="scope">{{scope.$index + 1}}</template>
    </el-table-column>
    <el-table-column
      prop="baseInfo.name"
      show-overflow-tooltip
      align="center"
      label="名称"
      width="180"
    ></el-table-column>
    <el-table-column prop="baseInfo.desc" show-overflow-tooltip header-align="center" label="简介"></el-table-column>
    <el-table-column :formatter="statusFormater" align="center" label="状态" width="80"></el-table-column>
    <el-table-column :formatter="handlerFormter" align="center" label="待处理部门" width="100"></el-table-column>
    <el-table-column
      show-overflow-tooltip
      :formatter="historyFormter"
      header-align="center"
      label="最新动态"
    ></el-table-column>
    <el-table-column prop="process" align="center" label="操作" width="100">
      <template slot-scope="scope">
        <el-button size="mini" @click="handleVisitHistory(scope.row)">查看历史</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'file',
  data() {
    return {}
  },
  computed: {
    tableData() {
      return this.$store.state.contracts.dataList
    }
  },
  methods: {
    handleVisitHistory(row) {
      this.$router.push({
        path: '/examine/history',
        query: {
          pid: this.$route.query.pid,
          cid: row.cid
        }
      })
    },
    statusFormater(row) {
      switch (row.status) {
        case -1:
          return '已废弃'
        case 0:
          return '拟稿中'
          break
        case 1:
          return '审核中'
        case 2:
          return '已完成'
        default:
          break
      }
    },
    handlerFormter(row) {
      let map = this.$store.state.department.deptMap
      return map[row.handler].name || ''
    },
    historyFormter(row) {
      return row.histories[row.histories.length - 1].info
    }
  }
}
</script>

<style scoped>
.table {
  width: 100%;
}

.table >>> .cell {
  text-overflow: unset !important;
}
</style>



