<template>
  <div class="recent">
    <div class="title-line">
      <span class="commend-tem">最近使用的模板</span>
      <div class="recent-clean-con">
      <el-button type="danger" plain @click="cleanRecent">清空记录</el-button>
      </div>
    </div>
    <el-table
      :data="recentList"
      stripe
      @row-click="handlePreview"
    >
      <el-table-column
        prop="id"
        label="编号"
        min-width="10%"
      ></el-table-column>
      <el-table-column
        prop="name"
        label="模板名称"
        min-width="45%"
      ></el-table-column>
      <el-table-column
        prop="lib"
        label="所属模板库"
        min-width="15%"
      ></el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间"
        min-width="15%"
      ></el-table-column>
      <el-table-column
        prop="lastTime"
        label="最近编辑"
        min-width="15%"
      ></el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      small
      layout="prev, pager, next"
      class="page-line"
      :current-page="currpage"
      :page-size="pagesize"
      :total="recentInfo.length"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
import * as Service from "@/action/user";
import * as TemplateService from "@/action/template";
import { formatTime } from "@/utils";
export default {
  name: "recent",
  created() {
    this.getRecent();
  },
  mounted() {
    this.pagesize = this.calculatePagesize();
  },
  computed: {
    recentList() {
      let size = this.pagesize;
      let curr = this.currpage;
      return this.recentInfo.filter(
        (v, index) => size * curr - size <= index && index < size * curr
      );
    }
  },
  methods: {
    //  初始化收藏数据
    async getRecent() {
      let recentTids = localStorage.getItem("recentTids");
      let recentInfo = [];
      if (recentTids) {
        let getRecentInfo = await TemplateService.getTemplatesByIds(recentTids);
        getRecentInfo.forEach((value, index) => {
          let libName = "";
          this.$store.state.user.libs.forEach(v => {
            if (v.tgid == value.group) {
              libName = v.category;
            }
          });
          recentInfo.push({
            id: ++index,
            name: value.name,
            lib: libName,
            createTime: formatTime(value.createTime),
            updateTime: formatTime(value.updateTime),
            tid: value.tid,
            group: value.group
          });
        });
        this.recentInfo = recentInfo;
      }
    },
    // 计算每页最合适的数量
    calculatePagesize() {
      let headH = 60,
        unitH = 48,
        bottomH = 46;
      let totalH = document.getElementsByClassName("work-content")[0]
        .clientHeight;
      return Math.floor((totalH - headH - bottomH) / unitH - 2);
    },
    // 页数改变
    currentChange(arg) {
      this.currpage = arg;
    },
    // 回到第一页
    resetPagination() {
      this.currpage = 1;
    },
    handlePreview(row) {
      this.$router.push({
        path: "/office",
        query: {
          mode: "view",
          template: row.tid,
          group: row.group
        }
      });
    },
    cleanRecent() {
      try {
        localStorage.removeItem('recentTids')
        this.$message({
          message: '清空成功',
          type: 'success'
        });
      } catch (error) {
        this.$message({
          message: '清空失败',
          type: 'error'
        });
      }
    }
  },
  data() {
    return {
      recentInfo: [],
      pagesize: 0,
      currpage: 1
    };
  }
};
</script>

<style scoped>
.recent {
  margin-left: 2%;
  height: 100%;
  position: relative;
}
.commend-tem {
  font-size: 24px;
}
.title-line {
  display: flex;
  align-items: center;
  height: 60px;
  line-height: 60px;
  margin-bottom: 0px;
  position: relative;
}
.page-line {
  text-align: center;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 20px;
  margin: 0 auto;
}
.recent-clean-con {
  position: absolute;
  right: 35px;
}
</style>



