<template>
  <div>
    <div class="title-line">上传合同稿件到 {{project.baseInfo.name}}</div>
    <div class="upload-container">
      <div class="upload-box" v-show="isChoosing">
        <el-upload
          ref="upload"
          drag
          :headers="headers"
          :action="uploadUri"
          :on-success="onUploadSuccess"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将文件拖到此处，或
            <em>点击上传</em>
          </div>
          <div class="el-upload__tip" slot="tip">只能上传doc/docx文件</div>
        </el-upload>
      </div>

      <el-form label-width="60px" class="form" v-show="!isChoosing">
        <el-form-item label="名称" required>
          <el-input v-model="form.baseInfo.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <br />
        <el-form-item label="标签">
          <tags :tags="this.form.tags" />
        </el-form-item>
        <br />
        <el-form-item label="描述">
          <el-input
            v-model="form.baseInfo.desc"
            type="textarea"
            :rows="5"
            placeholder="请输入模板描述，多模板将共享描述"
          ></el-input>
        </el-form-item>
        <br />
        <br />
        <div class="btn-group">
          <el-button @click="handleCancel">取消</el-button>
          <el-button @click="handleSubmit">添加到项目</el-button>
        </div>
      </el-form>

      <div class="preview-box" v-show="!isChoosing" v-loading="loading">
        <img :src="previewUrl" alt="预览图" />
      </div>
    </div>
  </div>
</template>

<script>
import * as Server from '@/action/contract'
import { filePreview } from '@/action/open'
import Tags from '@/components/Tags'
export default {
  name: 'upload',
  components: { Tags },
  data() {
    return {
      loading: false,
      previewUrl: '',
      isChoosing: true,
      headers: {
        Authorization: this.$store.getters.token
      },
      origin: {},
      form: {
        contract: '',
        tags: [],
        baseInfo: {
          name: '',
          type: '',
          tag: '',
          project: '',
          desc: ''
        }
      }
    }
  },
  computed: {
    project() {
      return (
        this.$store.state.projects.projectMap[this.pid] || {
          baseInfo: { name: '未知' }
        }
      )
    },
    pid() {
      return this.$route.query.pid
    },
    uploadUri() {
      return Server.uploadContract(this.$route.query.pid)
    }
  },
  created() {
    this.group = this.$route.query.group
  },
  methods: {
    // 监听上传成功
    onUploadSuccess(res) {
      this.origin = res
      this.form.contract = res.cid
      this.form.baseInfo = res.baseInfo
      this.isChoosing = false
      this.getPreviewPng()
    },
    // 重置表单
    handleReset() {
      this.form = {
        contract: '',
        tags: [],
        baseInfo: {
          name: '',
          type: '',
          tag: '',
          project: '',
          desc: ''
        }
      }
      this.$refs.upload.clearFiles()
    },
    async getPreviewPng() {
      this.loading = true
      let C = this.origin
      this.previewUrl = await filePreview(
        C.cid,
        C.baseInfo.name,
        C.baseInfo.type,
        C.uri
      )
      this.loading = false
    },
    // 取消上传
    handleCancel() {
      this.handleReset()
      this.isChoosing = true
    },
    // 提交表单
    async handleSubmit(e) {
      e.stopPropagation()
      let form = this.form
      if (form.baseInfo.name == '')
        return this.$message.warning('文件名称不可为空')

      form.baseInfo.tag = form.tags.join(' ')
      form.baseInfo.project = this.project.pid

      await Server.confirmAddContract(
        this.project.pid,
        { contract: form.contract },
        form.baseInfo
      )
      this.$confirm('上传成功，是否前往项目查看', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '继续上传',
        type: 'success'
      })
        .then(_ => {
          this.$router.push({
            path: '/examine/projects',
            query: {
              pid: this.pid
            }
          })
        })
        .catch(_ => {
          this.handleCancel()
        })
    }
  }
}
</script>

<style lang="less" scoped>
.upload-container {
  height: 75vh;
  display: flex;
}

.title-line {
  font-size: 24px;
  height: 60px;
  line-height: 60px;
  margin: 0 20px 20px;
}

.upload-box {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.form {
  margin-left: 20px;
  padding: 40px;
  margin-right: 40px;
  background-color: white;
  box-sizing: border-box;
  border-radius: 5px;
  width: 400px;
  height: 95%;
}

.preview-box {
  flex: 1;
  height: 95%;
  background-color: white;
  border-radius: 5px;
  text-align: center;
}

.preview-box img {
  height: 100%;
}

.btn-group {
  display: flex;
  justify-content: space-around;
}
</style>



