import request from "./request";

/**
 * 发送验证码
 */
export function getCaptcha(phone) {
  return request({
    method: "PUT",
    url: `/open-api/sms/${phone}/captcha`
  });
}

/**
 * 文件预览
 */
export function filePreview(
  cid,
  name,
  type,
  uri,
  height = 900,
  width = 630,
  first = true
) {
  return request({
    method: "POST",
    url: "/open-api/office/convert",
    data: {
      filetype: type,
      key: cid,
      outputtype: "png",
      thumbnail: {
        aspect: 1,
        first,
        height,
        width
      },
      title: name + "." + type,
      url: uri
    }
  }).then(data => data.fileUrl.replace("localhost", "47.100.222.159"));
}



