<template>
  <div>
    <div class="title-line">创建模板库</div>
    <el-form label-width="80px" class="form">
      <el-form-item label="名称" required>
        <el-input style="width: 200px" v-model="form.category" placeholder="请输入名称"></el-input>
      </el-form-item>
      <el-form-item label="描述">
        <el-input v-model="form.info" type="textarea" :rows="2" placeholder="请输入模板库描述"></el-input>
      </el-form-item>
      <el-form-item>
        <el-upload
          ref="upload"
          multiple
          accept=".doc, .docx"
          :on-exceed="onExceed"
          :limit="7"
          :headers="headers"
          action="http://legal.tgozzz.cn/templates/empty"
        >
          <el-button size="mini" type="primary">选择文件</el-button>
          <div slot="tip" class="el-upload__tip">只能上传doc/docx文件</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="handleSubmit">创建模板库</el-button>
        <el-button size="small" @click="handleReset">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { addTempLib, addTemp } from '@/action/template'
export default {
  name: 'addTemplateLib',
  data() {
    return {
      headers: {
        Authorization: this.$store.getters.token
      },
      form: {
        category: '',
        permission: 'extend',
        info: ''
      }
    }
  },
  methods: {
    // 文件数量超出限制
    onExceed() {
      this.$message.warning('不好意思，当前限制选择 7 个文件')
    },
    // 重置
    handleReset() {
      this.form.category = ''
      this.form.info = ''
      this.$refs.upload.clearFiles()
    },
    // 创建模板库
    async handleSubmit() {
      let lib = await addTempLib(this.form)
      let temps = {
        list: this.$refs.upload.uploadFiles.map(e => ({
          tid: e.response.tid,
          info: ''
        })),
        info: '初始化模板库'
      }
      if (temps.list.length != 0) await addTemp(temps, lib.tgid)
      await this.$confirm('上传成功', '提示', {
        confirmButtonText: '确定',
        type: 'success',
        showCancelButton: false
      })
      this.$router.push({
        path: '/template/library'
      })
    }
  }
}
</script>

<style scoped>
.title-line {
  font-size: 24px;
  height: 60px;
  line-height: 60px;
  margin: 0 20px 20px;
}
.form {
  width: 70%;
}
</style>



