import request from "./request";

/**
 * 添加项目
 * ```
 * {
 *  "status": "项目状态",
 *  "baseInfo": { // 基本信息
 *      "name": "名称",
 *      "tag": "标签",
 *      "partner": "合作公司",
 *      "desc": "描述"
 *  },
 *  "director": { // 负责人
 *      "uid": "id",
 *      "name": "名字",
 *      "phone": "联系电话"
 *  },
 *  "drafter": { // 拟稿部门
 *      "did": "部门id",
 *      "name": "名字",
 *      "phone": "联系电话",
 *      "responsibility": "职责描述"
 *  },
 *  "auditor": [{ // 审核部门列表
 *      "did": "部门id",
 *      "order": "顺序",
 *      "name": "名称",
 *      "phone": "联系电话",
 *      "responsibility": "职责描述"
 *  }]
 *}
 * ```
 * @param {} data 项目信息
 */
export function addProject(data) {
  return request({
    method: "POST",
    url: "/project",
    data
  });
}

/**
 * 获取与我相关项目
 * ```
 * return {{
    creator: [],
  	director: [],
    drafter: [],
    auditor: []
  }} //项目分类列表
  ```
 */
export function listMyProject() {
  return request({
    method: "GET",
    url: "/project"
  });
}

/**
 * 修改项目基本信息
 * @param {string} pid 项目id
 * @param {{
    "name": "名称",
    "tag": "标签",
    "partner": "合作公司",
    "desc": "描述"
  }} data 项目基本信息
 */
export function updateProject(pid, data) {
  return request({
    method: "PUT",
    url: `/project/${pid}`,
    data
  });
}

/**
 * 删除项目
 * @param {string} pid 项目id
 */
export function deleteProject(pid) {
  return request({
    method: "DELETE",
    url: `/project/${pid}`
  });
}

/**
 * 获取指定项目信息
 * @param {string} pid 项目id
 */
export function getProjectInfo(pid) {
  return request({
    method: "GET",
    url: `/project/${pid}`
  });
}

/**
 * 停止项目
 * @param {string} pid 项目id
 */
export function stopProject(pid) {
  return request({
    method: "PUT",
    url: `/project/${pid}/status/stop`
  });
}

/**
 * 启动项目
 * @param {string} pid 项目id
 */
export function restartProject(pid) {
  return request({
    method: "PUT",
    url: `/project/${pid}/status/restart`
  });
}



