<template>
  <div
    :id="id"
    tabindex="0"
    :class="`file-box ${isActive ? 'active' : ''}`"
    @blur="handleBlur"
    @focus="handleFocus"
    @contextmenu.prevent="handleOpenContextmenu"
  >
    <img class="file-icon" src="./word.svg" alt="word" />
    <span class="file-name">{{contract.baseInfo.name}}</span>

    <v-contextmenu ref="contextmenu" theme="bright">
      <v-contextmenu-item @click="handleDownload">下载</v-contextmenu-item>
      <v-contextmenu-item @click="handleView" v-if="mode == 'view'">预览</v-contextmenu-item>
      <v-contextmenu-item @click="handleEdit" v-if="mode == 'draft'">编辑</v-contextmenu-item>
      <v-contextmenu-item @click="handleAudit" v-if="mode == 'audit'">审核</v-contextmenu-item>
      <v-contextmenu-item @click="handleRename">重命名</v-contextmenu-item>
      <v-contextmenu-item divider></v-contextmenu-item>
      <v-contextmenu-item @click="handleView">属性</v-contextmenu-item>
      <v-contextmenu-item @click="handleToHistory">历史记录</v-contextmenu-item>
    </v-contextmenu>
  </div>
</template>

<script>
import {
  createContract,
  downloadWithSignature,
  rename,
} from '@/action/contract'
import { getElementLeft, getElementTop } from '@/utils'
export default {
  name: 'WordItem',
  props: {
    contract: {
      type: Object,
      default() {
        return {
          cid: '',
          baseInfo: {
            name: '',
            project: '',
          },
        }
      },
    },
    mode: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      isActive: false,
      isMenushow: false,
      id: 'word' + Date.now(),
    }
  },
  computed: {
    cid() {
      return this.contract.cid
    },
    pid() {
      return this.contract.baseInfo.project
    },
    filename() {
      return this.contract.baseInfo.name
    },
    filetype() {
      return this.contract.baseInfo.type
    },
  },
  created() {},
  methods: {
    getContextmenuStyle() {
      let ele = document.getElementById(this.id)
      let x = getElementLeft(ele)
      let y = getElementTop(ele)
      return {
        left: x + ele.offsetWidth / 2 + 'px',
        top: y + ele.offsetHeight / 2 + 'px',
      }
    },
    handleBlur() {
      this.isActive = false
      setTimeout((_) => {
        if (this.$refs.contextmenu) this.hideContextmenu()
      }, 300)
    },
    handleFocus() {
      this.isActive = true
    },
    hideContextmenu() {
      let contextmenu = this.$refs.contextmenu
      contextmenu.hide()
    },
    handleOpenContextmenu() {
      let contextmenu = this.$refs.contextmenu
      contextmenu.style = this.getContextmenuStyle()
      contextmenu.show()
    },
    handleDownload() {
      downloadWithSignature(
        this.pid,
        this.cid,
        this.filename,
        this.filetype,
        this.$store.getters.token
      )
    },
    handleEdit() {
      this.$router.push({
        path: '/office',
        query: {
          contract: this.cid,
          project: this.pid,
          mode: 'draft',
        },
      })
    },
    handleAudit() {
      this.$router.push({
        path: '/office',
        query: {
          contract: this.cid,
          project: this.pid,
          mode: 'audit',
        },
      })
    },
    handleRename() {
      this.$prompt('请输入文件名称', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: this.contract.baseInfo.name,
        inputValidator(v) {
          if (v == '') return '请输入名称'
          return true
        },
      })
        .then(async ({ value }) => {
          await rename(this.pid, this.cid, { name: value })
          await this.$store.dispatch('contracts/getListByPid', this.pid)
          this.$message({
            type: 'success',
            message: '重命名成功',
          })
        })
        .catch((_) => {
          this.$message({
            type: 'info',
            message: '取消输入',
          })
        })
    },
    handleView() {
      this.$router.push({
        path: '/office',
        query: {
          contract: this.cid,
          project: this.pid,
          mode: 'preview',
        },
      })
    },
    handleToHistory() {
      this.$router.push({
        path: '/examine/history',
        query: {
          pid: this.pid,
          cid: this.cid,
        },
      })
    },
  },
}
</script>

<style scoped>
.file-box {
  width: 90px;
  height: 100px;
  margin: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  outline: none;
  overflow: visible;
}

.file-icon {
  width: 50px;
  height: 50px;
  outline: none;
}

.file-name {
  display: inline-block;
  width: 100%;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  font-family: MicrosoftJhengHei;
  outline: none;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  z-index: 99;
}

.active .file-name {
  overflow: visible;
  text-overflow: unset;
  -webkit-line-clamp: unset;
}
</style>



