import request from "./request";

/**
 * 获取一条通知
 */
export function getNotice(nid) {
  return request({
    method: 'GET',
    url: `/notice/${nid}`
  })
}

/**
 * 获取通知数组
 */
export function listNotice(nids) {
  return request({
    method: 'POST',
    url: '/notice/list',
    data: nids
  })
}



