import request from "./request";

/**
 * 申请编辑模板
 */
export function applyToEdit(tgid, tid, mode = "extend") {
  if (!tgid || !tid) return Promise.reject("参数错误");

  return request({
    method: "PUT",
    url: `/templates/${tgid}/${tid}?mode=${mode}`
  });
}

/**
 * 保存编辑的模板
 * @param {{
    name: "",
    group: "",
    info: "",
    updateInfo: ""
  }} data
 */
export function updateTemp(data, oldTgid, tid) {
  let bean = {
    name: "",
    group: "",
    info: "",
    updateInfo: ""
  };

  if (!data.group) return Promise.reject("参数错误");

  return request({
    method: "PATCH",
    url: `/templates/${oldTgid}/${tid}`,
    data: Object.assign({}, bean, data)
  });
}

/**
 * 获取模板库列表
 */
export function listTemplateLibs() {
  return request({
    method: "GET",
    url: "/templates"
  });
}

/**
 * 获取模板列表
 */
export function listTemps(tgid) {
  return request({
    method: "GET",
    url: `/templates/${tgid}`
  });
}

/**
 * 根据名字搜索模板
 */
export function searchTempByName(name) {
  if (!name) return Promise.reject("参数错误");

  return request({
    method: "GET",
    url: `/templates`,
    params: {
      name
    }
  });
}

/**
 * 删除模板
 */
export function deleteTemp(tgid, tid) {
  if (!tgid || !tid) return Promise.reject("参数错误");

  return request({
    method: "DELETE",
    url: `/templates/${tgid}/${tid}`
  });
}

/**
 * 删除模板库
 */
export function deleteTempLib(tgid) {
  if (!tgid) return Promise.reject("参数错误");

  return request({
    method: "DELETE",
    url: `/templates/${tgid}`
  });
}

/**
 * 添加模板库
 * @param {{
    "category": "租赁合同",
    "permission": "extend",
    "info": "呵~"
  }} data
 */
export function addTempLib(data) {
  if (!data) return Promise.reject("参数错误");

  return request({
    method: "POST",
    url: "/templates",
    data
  });
}

/**
 * 上传模板
 */
export function uploadTemp(file, tgid = "empty") {
  return request({
    method: `/templates/${tgid}`,
    url: `/templates/${tgid}`,
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data: file
  });
}

/**
 * 确认添加模板到模板库
 * @param {{
    "list": [
      {
        tid:"5ea83e37e2968353963c3ba1",
        info: "模板信息"
      }
    ],
    "info": "新增模板 XXX"
  }} data
 * @param {String} tgid
 */
export function addTemp(data, tgid) {
  if (!data.list || data.list.length == 0) return Promise.reject("参数无效");

  return request({
    method: "POST",
    url: `/templates/${tgid}`,
    data
  });
}

/**
 * 获取指定模板信息
 */
export function getTempInfo(tgid, tid) {
  return request({
    method: "GET",
    url: `/templates/${tgid}/${tid}`
  });
}

/**
 * 添加到收藏夹
 */
export function addToFavorites(uid, tid) {
  return request({
    method: "POST",
    url: `/users/${uid}/template/mark`,
    data: {
      tid
    }
  });
}

/**
 * 取消
 */
export function removeFromFavorites(uid, tid) {
  return request({
    method: "DELETE",
    url: `/users/${uid}/template/mark/${tid}`
  });
}

/**
 * 模板id数组获取模板信息数组
 */
export function getTemplatesByIds(data) {
  return request({
    method: "POST",
    url: `/templates/list`,
    data
  });
}


