<template>
  <div class="login-container">
    <div class="form">
      <login-header />
      <el-menu default-active="1" class="menu" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">登录</el-menu-item>
        <el-menu-item index="2">注册</el-menu-item>
      </el-menu>
      <div class="input-group" v-if="activeIndex == '1'">
        <el-input class="input" v-model="loginForm.phone" placeholder="请输入手机号码" />
        <el-input class="input" v-model="loginForm.password" placeholder="请输入密码" show-password />
        <el-button class="button" @click="handleLogin" type="primary">登录</el-button>
      </div>
      <div class="input-group" v-else>
        <el-input class="input" v-model="registForm.phone" placeholder="请输入手机号码" />
        <div class="input-line">
          <el-input class="input-small" v-model="registForm.captcha" placeholder="请输入短信验证码" />
          <el-button
            class="button-samll"
            plain
            type="primary"
            @click="handleSendSms"
            :disabled="smsTime !== 0"
          >{{btnContent}}</el-button>
        </div>
        <el-input class="input" placeholder="请输入密码" v-model="registForm.password" show-password />
        <el-button class="button" @click="handleRegist" type="primary">注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import LoginHeader from '@/pages/user/common/Header'
import { getCaptcha } from '@/action/open'
export default {
  name: 'login',
  components: { LoginHeader },
  data() {
    return {
      activeIndex: '1',
      loginForm: {
        phone: '',
        password: ''
      },
      registForm: {
        phone: '',
        captcha: '',
        password: ''
      },
      smsTime: 0,
      btnContent: '发送验证码'
    }
  },
  methods: {
    // 切换menu
    handleSelect(key) {
      this.activeIndex = key
    },
    // 发送短信验证码
    async handleSendSms() {
      let phone = this.registForm.phone
      if (!this.check(phone)) return

      this.smsTime = 60
      this.btnContent = this.smsTime + 's后重新发送'

      await getCaptcha(phone)
      let timer = setInterval(() => {
        if (this.smsTime !== 0) {
          this.btnContent = --this.smsTime + 's后重新发送'
        } else {
          this.btnContent = '发送短信验证码'
          clearInterval(timer)
        }
      }, 1000)
    },
    // 注册
    async handleRegist() {
      if (!this.check()) return
      try {
        await this.$store.dispatch('user/registWithPhone', this.registForm)
        this.$message.success('注册成功')
        setTimeout(() => this.$router.push(this.$route.query.redirect || '/'), 1000)
      } catch (err) {
        this.$message.error('注册失败，请核对信息')
      }
    },
    // 登录
    async handleLogin() {
      if (!this.check()) return
      try {
        await this.$store.dispatch('user/loginWithPw', this.loginForm)
        this.$message.success('登录成功')
        setTimeout(() => this.$router.push(this.$route.query.redirect || '/'), 1000)
      } catch (err) {
        console.log(err.response)
        this.$message.error('登录失败，请核对信息')
      }
    },
    // 检查参数
    check(phone) {
      const phoneRegExp = new RegExp(/^1[0-9]{10}$/)
      if (phone) return phoneRegExp.test(phone)

      let flag = true
      let f1 = this.loginForm
      let f2 = this.registForm

      flag =
        this.activeIndex == '1'
          ? phoneRegExp.test(f1.phone) && f1.password
          : phoneRegExp.test(f2.phone) && f2.password && f2.captcha

      if (!flag) {
        this.$alert('请输入正确的信息！', '提示', {
          confirmButtonText: '确认'
        })
      }
      return flag
    }
  }
}
</script>
<style scoped>
.login-container {
  height: 70vh;
  padding: 15vh;
  background: linear-gradient(to right, #2b819e 40%, #bad7e1 40%);
}
.menu {
  width: 30.4%;
  margin: 15px auto;
}
.form {
  width: 450px;
  height: 70vh;
  box-sizing: border-box;
  padding-top: 40px;
  margin: 0 auto;
  min-height: 480px;
  background: white;
  border-radius: 20px;
  box-shadow: 0 40px 40px rgba(0, 0, 0, 0.12), 0 0 60px rgba(0, 0, 0, 0.04);
}
.input-group {
  text-align: center;
}
.input {
  margin-top: 30px;
  width: 70%;
}
.button {
  margin-top: 8%;
  width: 35%;
}
.input-line {
  display: inline-flex;
  justify-content: space-between;
  margin-top: 30px;
  width: 70%;
}
.input-small {
  width: 55%;
}
.button-samll {
  width: 40%;
}
</style>



