import Home from "@/pages/home";
import HomeBody from "@/pages/home/components/HomeBody";
import Apply from "@/pages/home/components/apply";
import Solve from "@/pages/home/components/solve";
import Touch from "@/pages/home/components/touch";

export default {
  path: "/",
  name: "Home",
  redirect: "/body",
  component: Home,
  children: [
    {
      path: "body",
      name: "body",
      component: HomeBody
    },
    {
      path: "apply",
      name: "apply",
      component: Apply
    },
    {
      path: "solve",
      name: "solve",
      component: Solve
    },
    {
      path: "touch",
      name: "touch",
      component: Touch
    }
  ]
};



