<template>
  <div class="nav">
    <div class="nav-in">
      <div class="logo" @click="toHome">
        <img class="logo-img" src="/static/logo.png" alt />
      </div>
      <div class="nav-items">
        <div class="nav-item" @click="toApply">
          <i class="el-icon-menu"></i> 产品应用
        </div>
        <div class="nav-item" @click="toSolve">
          <i class="el-icon-s-order"></i>
          解决方案
        </div>
        <div class="nav-item" @click="toTouch">
          <i class="el-icon-phone"></i> 联系我们
        </div>
        <div class="nav-item"></div>
      </div>
      <div class="nav-right">
        <div class="nav-right-item">
          <a style="color:white" href="http://legal.tgozzz.cn/admin/index.html">
            <i class="el-icon-warning-outline"></i> 后台管理
          </a>
        </div>
        <div v-if="avater == ''" class="nav-right-item login-but" @click="toLogin">
          <i class="el-icon-user-solid"></i> 登录
        </div>
        <div v-else class="nav-right-item">
          <router-link to="/personal">
            <img class="avater-head" :src="avater" />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeNav',
  data() {
    return {}
  },
  computed: {
    avater() {
      return this.$store.state.user.portrait || ''
    }
  },
  methods: {
    toApply() {
      this.$router.push('/apply')
    },
    toHome() {
      this.$router.push('/')
    },
    toSolve() {
      this.$router.push('/solve')
    },
    toLogin() {
      this.$router.push('/login')
    },
    toTouch() {
      this.$router.push('/touch')
    }
  }
}
</script>

<style scoped>
.nav {
  position: fixed;
  top: 0;
  height: 59px;
  width: 100vw;
  /* box-sizing: border-box; */
  background: #2a5298;
  display: flex;
  align-items: center;
  z-index: 5;
}
.nav-in {
  width: 70%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  color: white;
  position: relative;
}
.logo-img {
  height: 33px;
}
.logo {
  cursor: pointer;
}
.nav-items {
  display: flex;
  align-items: center;
  font-weight: bold;
  margin-left: 30px;
}
.nav-item {
  margin-left: 30px;
  cursor: pointer;
}
.nav-right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  display: flex;
  align-items: center;
}
.nav-right-item {
  margin-left: 30px;
  font-weight: bold;
  cursor: pointer;
}
.login-but {
  border: white solid 2px;
  border-radius: 20px;
  padding: 5px 20px;
  cursor: pointer;
  transition: all 0.3s;
}
.login-but:hover {
  background: white;
  color: #2a5298;
}
.avater-head {
  width: 35px;
  height: 35px;
  opacity: 100%;
  border-radius: 50%;
}
</style>



