import router from "./router";
import store from "./store";
import NProgress from "nprogress";
import "nprogress/nprogress.css";

const whiteList = ["/login", "/body", "/apply", "/touch", "/solve"]; // 不必校验是否登录
const tryLoginList = ["/body"];

router.beforeEach(async (to, from, next) => {
  NProgress.start();
  const hasToken = store.getters.hasToken;
  const hasLogin = store.getters.hasLogin;

  for (const whiteItem of whiteList) {
    //白名单放行
    if (to.path.indexOf(whiteItem) != -1) {
      if (hasToken && to.path.indexOf(tryLoginList) != -1)
        await store.dispatch("user/loginWithToken");
      return next();
    }
  }

  if (!hasLogin) {
    // 没有登录
    if (!hasToken) {
      // 没有token记录，直接前往登录页面
      return next(`/login?redirect=${to.path}`);
    } else {
      if (await store.dispatch("user/loginWithToken")) {
        // 通过token记录登录成功
        return next();
      } else {
        // 通过token记录登录失败，前往登录页面
        return next(`/login?redirect=${to.path}`);
      }
    }
  }

  return next();
});

router.afterEach(() => {
  NProgress.done();
});



