<template>
  <div>
    <div class="title-line">
      <div class="title-line-left">
        <div class="title-line-name">微泛 WeiFan</div>
        <div class="title-line-desc">企业合同审核管理 ,</div>
        <div class="title-line-desc">连接企业人员、项目与合同。</div>
        <router-link to="/examine/myproject">
          <div class="use-but">立即使用</div>
        </router-link>
      </div>
      <div class="title-line-right"></div>
    </div>
    <div class="desc-box">
      <img class="desc" src="/static/desc.png" alt />
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeBody",
  methods: {}
};
</script>

<style scoped>
.title-line {
  height: 500px;
  width: 100vw;
  background-image: url("/static/buildings.png"),
    linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
  margin-top: 59px;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  box-sizing: border-box;
  padding-left: 15%;
  align-items: center;
}
.title-line::before {
  margin-right: 0;
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  top: 500px;
  height: 0;
  border-style: solid;
  border-width: 60px 0 0 100vw;
  border-color: transparent transparent transparent #f9faff;
}
.title-line-name {
  color: white;
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 15px;
}
.title-line-desc {
  color: white;
  font-size: 30px;
  margin-top: 10px;
  font-weight: 100;
}
.use-but {
  border: white solid 2px;
  display: inline-block;
  color: white;
  font-size: 18px;
  font-weight: bold;
  padding: 6px 25px;
  margin-top: 20px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}
.use-but:hover {
  background: white;
  color: #2a5298;
}
.desc {
  width: 100%;
}
.desc-box {
  width: 100vw;
  margin-bottom: 80px;
}
</style>


