import Personal from "@/pages/personal"
import Base from "@/pages/personal/base"
import Department from "@/pages/personal/department"

export default {
  path: "/personal",
  name: "Personal",
  redirect: "/personal/base",
  component: Personal,
  children: [
    {
      path: "base",
      name: "Base",
      component: Base
    },
    {
      path: "department",
      name: "Department",
      component: Department
    }
  ]
};



