import Vue from "vue";
import * as Server from "@/action/contract";

const state = {
  dataList: [],
  contractMap: {}
};

const mutations = {
  init(state, data) {
    data.sort(
      (e1, e2) =>
        e2.histories[e2.histories.length - 1].time -
        e1.histories[e1.histories.length - 1].time
    );
    state.dataList = data;
    data.forEach(e => {
      Vue.set(state.contractMap, e.cid, e);
    });
  },
  clear(state) {
    state.dataList = [];
    state.contractMap = {};
  }
};

const actions = {
  async getListByPid({ commit }, pid) {
    commit("clear");
    commit("init", await Server.listContract(pid));
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};



