import request from "./request";
import { BASE_URL } from "./request/config";

/**
 * 列出项目下所有合同
 */
export function listContract(pid = "") {
  return request({
    method: "GET",
    url: `/project/${pid}/contracts`
  });
}

/**
 * 上传合同
 * 仅仅上传
 * 设置uri
 * 设置创建者信息
 * 设置默认基本信息
 * 添加一条历史记录
 */
export function uploadContract(pid = "") {
  return `${BASE_URL}/project/${pid}/contracts`;
}

/**
 * 上传临时合同文件
 */
export function uploadTempContract() {
  return `${BASE_URL}/project/temp/contracts`;
}

/**
 * copy一份模板
 * 模板使用数量+1
 * 从模板库创建合同
 * 仅仅创建
 * 设置创建者信息
 * 继承模板相关信息
 * 添加一条历史记录
 */
export function createContract(pid = "", query = { template: "" }) {
  return request({
    method: "POST",
    url: `/project/${pid}/contracts?template=${query.template}`
    // params: query
  });
}

/**
 * 确认添加合同到项目
 * 保存基本信息
 * 项目添加一条历史记录
 * 合同添加一条历史记录
 * 正式开始编辑
 * 设置handler
 * 设置status
 */
export function confirmAddContract(
  pid = "",
  query = { contract: "" },
  data = { name: "", type: "", tag: "", project: "", desc: "" }
) {
  return request({
    method: "PATCH",
    url: `/project/${pid}/contracts`,
    params: query,
    data
  });
}

/**
 * 回退版本
 * 删除最新版本
 * 项目添加一条历史记录
 * 合同添加一条历史记录
 */
export function rollbackContract(
  pid = "",
  query = { rollback2: "", lastCid: "" }
) {
  return request({
    method: "PUT",
    url: `/project/${pid}/contracts`,
    params: query
  });
}

/**
 * 获取指定合同数据
 */
export function getContract(pid = "", cid = "") {
  return request({
    method: "GET",
    url: `/project/${pid}/contracts/${cid}`
  });
}

/**
 * 申请编辑合同
 * 创建新版本
 * 返回基本配置以及合同信息
 */
export function applyToEdit(pid = "", cid = "") {
  return request({
    method: "PUT",
    url: `/project/${pid}/contracts/${cid}`
  });
}

/**
 * 申请审核合同
 * 返回基本配置
 */
export function applyToAudit(pid = "", cid = "") {
  return request({
    method: "GET",
    url: `/project/${pid}/contracts/${cid}/audit`
  });
}

/**
 * 保存合同修改信息
 * 项目更新合同版本
 */
export function addEditInfo(
  pid = "",
  cid = "",
  data = { oldCid: "", info: { summary: "", details: "" } }
) {
  return request({
    method: "PATCH",
    url: `/project/${pid}/contracts/${cid}`,
    data
  });
}

/**
 * 作废合同
 * 合同添加作废记录
 * 项目添加销毁记录
 */
export function deleteContract(pid = "", cid = "") {
  return request({
    method: "DELETE",
    url: `/project/${pid}/contracts/${cid}`
  });
}

/**
 * 添加审核意见
 * 添加历史记录
 */
export function addAuditInfo(
  pid = "",
  cid = "",
  data = { summary: "", details: "" }
) {
  return request({
    method: "POST",
    url: `/project/${pid}/contracts/${cid}/audit`,
    data
  });
}

/**
 * 回到编辑环节
 * 新增版本
 * 项目内修改合同版本
 * 添加合同的历史记录
 * 设置status
 * 设置handler
 */
export function moveToEdit(pid = "", cid = "") {
  return request({
    method: "POST",
    url: `/project/${pid}/${cid}/back`
  });
}

/**
 * 进入下一个步骤
 * 新增版本
 * 项目内修改合同版本
 * 添加合同的历史记录
 * 设置status
 * 设置handler
 * 合同完成时，添加项目历史记录
 */
export function moveToNext(pid = "", cid = "") {
  return request({
    method: "POST",
    url: `/project/${pid}/contracts/${cid}/next`
  });
}

/**
 * 下载word文件
 */
export function download(cid, name) {
  const url = `http://legal.tgozzz.cn/office/downloadWithName?fileId=${cid}&fileName=${name}`;
  var a = document.createElement("a");
  a.href = url;
  a.download = name;
  a.click();
}

/**
 * 下载word文件添加签名
 */
export function downloadWithSignature(pid, cid, name, type, token) {
  const url = `http://legal.tgozzz.cn/project/${pid}/contracts/${cid}/download?filename=temp.${type}&token=${token}`;
  var a = document.createElement("a");
  a.href = url;
  a.download = name + "." + type;
  a.click();
}

/**
 * 核验签名， 文件须先上传获取id
 */
export function verifySignature(cid) {
  return request({
    method: "GET",
    url: `/project/temp/contracts/${cid}/verify`
  });
}

/**
 * 重命名合同
 * 合同历史添加一条记录
 */
export function rename(pid = "", cid = "", data = { name: "" }) {
  return request({
    method: "PUT",
    url: `/project/${pid}/contracts/${cid}/baseinfo/name`,
    data
  });
}



