import Template from "@/pages/template";
import Library from "@/pages/template/library";
import Favorite from "@/pages/template/favorite";
import Mine from "@/pages/template/mine";
import Recent from "@/pages/template/recent";
import AddTemplate from "@/pages/template/common/AddTemplate";
import AddTemplateLib from "@/pages/template/common/AddTemplateLib";

export default {
  path: "/template",
  name: "pactTemplate",
  redirect: "/template/library",
  component: Template,
  children: [
    {
      path: "library",
      name: "library",
      component: Library
    },
    {
      path: "favorite",
      name: "favorite",
      component: Favorite
    },
    {
      path: "mine",
      name: "mine",
      component: Mine
    },
    {
      path: "recent",
      name: "recent",
      component: Recent
    },
    {
      path: "addTemplate",
      name: "addTemplate",
      component: AddTemplate
    },
    {
      path: "addTemplateLib",
      name: "addTemplateLib",
      component: AddTemplateLib
    }
  ]
};



