<template>
  <div class="create-project-container">
    <div class="in-container">
      <div class="title-text">
        创建项目
      </div>
      <div class="show-content">
        <step
          class="step-item"
          :activeStep="1"
        ></step>
        <div>
          <div class="form-box form-item">
            <el-form
              ref="form"
              :model="form"
              label-width="80px"
            >
              <el-form-item label="项目名称">
                <el-input v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="项目描述">
                <el-input
                  type="textarea"
                  class="form-desc"
                  v-model="form.desc"
                ></el-input>
              </el-form-item>

              <el-form-item label="标签">

                <div>
                  <el-tag
                    :key="tag"
                    v-for="tag in dynamicTags"
                    closable
                    class="tag"
                    :disable-transitions="false"
                    @close="handleClose(tag)"
                  >
                    {{tag}}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="inputVisible"
                    v-model="inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm"
                    @blur="handleInputConfirm"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput"
                  >+ 添加</el-button>
                </div>

              </el-form-item>

            </el-form>
          </div>
          <div class="form-item form-right">
            <el-form
              ref="form"
              :model="form"
              label-width="80px"
            >
              <el-form-item label="负责人">
                <el-select
                  v-model="form.leader"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in users"
                    :key="item.uid"
                    :label="item.name"
                    :value="item.uid"
                  >
                    <span style="float: left">{{ item.name }}</span>
                    <span style="float: right; color: #8492a6; font-size: 13px">{{ item.organization.department.name }}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item
                v-for="(cor, index) in form.cors"
                :label="'合作企业' +  ++index"
                :key="cor.key"
              >

                <el-input
                  class="corporates"
                  v-model="cor.value"
                >
                  <el-button
                    type="danger"
                    slot="append"
                    icon="el-icon-delete"
                    @click.prevent="removeCor(cor)"
                  >移除</el-button>
                </el-input>

              </el-form-item>

              <el-form-item>
                <el-button @click="addCor">添加合作企业</el-button>
                <!-- <router-link to='/examine/selectDepartment'> -->
                <el-button
                  type="primary"
                  @click="next"
                >下一步</el-button>
                <!-- </router-link> -->
              </el-form-item>

            </el-form>

          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Step from "./step";
import * as Service from "@/action/user";
export default {
  name: "WriteInfo",
  components: {
    Step
  },
  created() {
    this.getUsers();
  },
  methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },

    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    addCor() {
      this.form.cors.push({
        value: "",
        key: Date.now()
      });
    },
    removeCor(item) {
      var index = this.form.cors.indexOf(item);
      if (index !== -1) {
        this.form.cors.splice(index, 1);
      }
    },
    next() {
      if (!this.form.name) {
        this.$message({
          message: "项目名称不得为空",
          type: "warning"
        });
      } else if (!this.form.desc) {
        this.$message({
          message: "项目描述不得为空",
          type: "warning"
        });
      } else if (!this.form.leader) {
        this.$message({
          message: "项目负责人不得为空",
          type: "warning"
        });
      } else if (!this.form.cors.length) {
        this.$message({
          message: "合作企业不得为空",
          type: "warning"
        });
      } else {
        this.$router.push({
          name: "selectDepartment",
          params: {
            info: Object.assign(this.form, { tags: this.dynamicTags })
          }
        });
      }
    },
    async getUsers() {
      try {
        let users = await Service.getUsers();
        console.log(users);
        this.users = users;
      } catch (error) {
        console.log(error);
      }
    }
  },
  data() {
    return {
      form: {
        name: "",
        desc: "",
        leader: "",
        cors: []
      },
      users: [],
      dynamicTags: [],
      inputVisible: false,
      inputValue: ""
    };
  }
};
</script>

<style scoped>
.in-container {
  /* overflow: auto; */
  width: 98%;
  margin-left: 3%;
  height: 100%;
}
.create-project-container {
  height: 100%;
}
.title-text {
  margin-bottom: 10px;
  font-size: 20px;
}
.show-content {
  background: white;
  overflow: auto;
  width: 100%;
  min-height: 75vh;
  padding-top: 20px;
  box-sizing: border-box;
}
.step-item {
  width: 85%;
  margin: 0 auto;
}
.form-box {
  width: 45%;
  margin-left: 40px;
  margin-top: 40px;
}
.form-desc {
  height: 100px;
}
.form-desc >>> .el-textarea__inner {
  height: 100px;
}
.form-item {
  width: 38%;
  float: left;
  margin-left: 40px;
  margin-top: 40px;
}
.form-item >>> .el-cascader {
  width: 100%;
}
.form-right {
  margin-left: 80px;
}
.in-form-right {
  /* margin-left: 40px; */
  margin-top: 20px;
}
.leader-title {
  /* margin-top: 1%; */
  color: #666;
  font-size: 15px;
}
.tag {
  margin-left: 2px;
}
</style>


