<template>
  <div class="library-container" v-loading="loading">
    <div class="title-line">
      <!-- 三个标题 -->
      <span class="title" v-if="mode == 0">
        <span class="nav-title"><img class="nav-point" src="/static/nav-point.png" alt=""> &nbsp;模板库</span>
      </span>
      <span class="title" v-else-if="mode == 1">
        <i class="el-icon-arrow-left back-icon" @click="ListTempLibs" />
        {{lib.category}}
      </span>
      <span class="title" v-else>
        <i class="el-icon-arrow-left back-icon" @click="ListTempLibs" /> 搜索结果
      </span>

      <!-- 操作区 -->
      <div v-if="mode != 1">
        <el-input placeholder="请输入内容" v-model="query.name" class="search-input" clearable></el-input>
        <el-button type="primary" icon="el-icon-search" class="search-btn" @click="handleSearch">搜索</el-button>
        <el-button type="primary" icon="el-icon-plus" class="search-btn" @click="handleAddTempLib" plain>添加</el-button>
      </div>
      <div class="buts" v-if="mode == 1">
        <el-button type="primary" plain @click="handleAddTemp">添加模板</el-button>
        <el-button type="danger" plain @click="handleDeleteTempLib">删除模板库</el-button>
      </div>
    </div>

    <!-- 模板库表格 -->
    <el-table :data="libTableList" stripe @row-click="handleListTemp" v-if="mode == 0">
      <el-table-column prop="index" label="编号" width="180"></el-table-column>
      <el-table-column prop="category" label="模板库" show-overflow-tooltip width="180"></el-table-column>
      <el-table-column prop="count" label="范本数量" show-overflow-tooltip width="180"></el-table-column>
      <el-table-column prop="owner" label="创建者" show-overflow-tooltip></el-table-column>
      <el-table-column prop="createTime" label="创建时间" :formatter="timeFormat" show-overflow-tooltip></el-table-column>
    </el-table>

    <!-- 模板表格 -->
    <el-table :data="tempTableList" stripe @row-click="handlePreview" v-else>
      <el-table-column prop="index" label="编号" width="80"></el-table-column>
      <el-table-column prop="name" label="合同模板" show-overflow-tooltip min-width="200"></el-table-column>
      <el-table-column prop="owner" label="创建者" show-overflow-tooltip min-width="120"></el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间"
        show-overflow-tooltip
        :formatter="timeFormat"
        min-width="80"
      ></el-table-column>
      <el-table-column prop="updateInfo" fixed="right" label="最近更改" show-overflow-tooltip width="100"></el-table-column>
      <el-table-column label="操作" fixed="right" width="100">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleDeleteTemp(...arguments, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination
      small
      layout="prev, pager, next"
      class="page-line"
      :current-page="currpage"
      :page-size="pagesize"
      :total="totalItemsNum"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
import * as Service from '@/action/template'
import { formatTime } from '@/utils'
export default {
  name: 'TemplateLib',
  data() {
    return {
      pagesize: 0,
      currpage: 1,
      libData: [],
      lib: {
        tgid: '',
        category: ''
      },
      tempData: [],
      mode: 0,
      query: {
        name: ''
      },
      loading: false
    }
  },
  computed: {
    // 模板库列表
    libTableList() {
      let size = this.pagesize
      let curr = this.currpage
      return this.libData.filter(
        (v, index) => size * curr - size <= index && index < size * curr
      )
    },
    // 模板列表
    tempTableList() {
      let size = this.pagesize
      let curr = this.currpage
      return this.tempData.filter(
        (v, index) => size * curr - size <= index && index < size * curr
      )
    },
    // 总数
    totalItemsNum() {
      let l1 = this.libData.length
      let l2 = this.tempData.length
      return this.mode ? l2 : l1
    }
  },
  created() {
    this.ListTempLibs()
  },
  mounted() {
    this.pagesize = this.calculatePagesize()
  },
  methods: {
    // 获取模板库
    async ListTempLibs() {
      try {
        this.loading = true
        let libData = await Service.listTemplateLibs()
        this.libData = libData.map((e, index) => {
          e.index = index + 1
          return e
        })
      } catch (err) {
      } finally {
        this.resetPagination()
        this.mode = 0
        this.loading = false
      }
    },
    // 点击模板库，显示模板列表
    async handleListTemp(row) {
      try {
        this.loading = true
        let tempData = await Service.listTemps(row.tgid)
        this.tempData = tempData
          .sort((e1, e2) => e2.createTime - e1.createTime)
          .map((e, index) => {
            e.index = index + 1
            return e
          })
      } catch (err) {
      } finally {
        this.resetPagination()
        this.lib = row
        this.mode = 1
        this.loading = false
      }
    },
    // 搜索模板
    async handleSearch() {
      try {
        let tempData = await Service.searchTempByName(this.query.name)
        this.tempData = tempData
          .sort((e1, e2) => e2.createTime - e1.createTime)
          .map((e, index) => {
            e.index = index + 1
            return e
          })
      } catch (err) {
      } finally {
        this.resetPagination()
        this.mode = 2
        this.loading = false
      }
    },
    // 添加模板
    handleAddTemp() {
      this.$router.push({
        path: '/examine/addTemplate',
        query: { group: this.lib.tgid }
      })
    },
    // 添加模板库
    handleAddTempLib() {
      this.$router.push({ path: '/examine/addTemplateLib' })
    },
    // 删除模板
    handleDeleteTemp(e, row) {
      e.stopPropagation()
      this.$confirm(`确定删除模板 ${row.name} ？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await Service.deleteTemp(row.group, row.tid)
          await this.handleListTemp(this.lib)
        })
        .then(() => {
          this.$message({
            message: '删除成功',
            type: 'success',
            duration: 3 * 1000
          })
        })
        .catch(err => console.log('已取消', err))
    },
    // 删除模板库
    handleDeleteTempLib() {
      this.$confirm(`确定删除模板库 ${this.lib.category} ？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await Service.deleteTempLib(this.lib.tgid)
        })
        .then(() => {
          this.$message({
            message: '删除成功',
            type: 'success',
            duration: 3 * 1000
          })
          this.$router.push('/examine/templateLib')
        })
        .catch(err => console.log('已取消', err))
    },
    // 进入模板预览
    handlePreview(row) {
      this.$router.push({
        path: '/office',
        query: {
          mode: 'view',
          template: row.tid,
          group: row.group
        }
      })
    },
    // 格式化时间
    timeFormat(row) {
      return formatTime(row.createTime)
    },
    // 计算每页最合适的数量
    calculatePagesize() {
      let headH = 150,
        unitH = 48,
        bottomH = 46
      let totalH = document.getElementsByClassName('work-area-item')[0]
        .clientHeight
      return Math.floor((totalH - headH - bottomH) / unitH - 1)
    },
    // 页数改变
    currentChange(arg) {
      this.currpage = arg
    },
    // 回到第一页
    resetPagination() {
      this.currpage = 1
    }
  }
}
</script>

<style scoped>
.library-container {
  width: 98%;
  height: 100%;
  margin-left: 2%;
  position: relative;
}

.title-line {
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.title {
  font-size: 24px;
  line-height: 30px;
}

.add-icon {
  width: 30px;
  height: 30px;
  margin-left: 5px;
  vertical-align: middle;
  transition: all 0.2s;
}

.add-icon:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.back-icon {
  transition: all 0.2s;
}

.back-icon:hover {
  cursor: pointer;
  color: blue;
}

.buts {
  margin-right: 40px;
}

.search-input {
  width: 200px;
  margin-right: 10px;
}

.page-line {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: -40px;
}
</style>



