<template>
  <div>
    <div class="title-text">基本信息</div>
    <div class="info-box">

      <div class="head">
        <img
          class="head-img"
          :src="$store.state.user.portrait"
          alt=""
        >
      </div>
      <div class="form-box">
        <el-form
          :model="userForm"
          label-width="80px"
        >
          <el-form-item label="用户名">
            <el-input v-model="userForm.name"></el-input>
          </el-form-item>
          <el-form-item label="手机号码">
            <span class="phone">{{userForm.phone}}</span>
            <router-link to="/user/revisephone">修改</router-link>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group
              v-model="userForm.sex"
              size="small"
            >
              <el-radio
                :label="true"
                border
              >男</el-radio>
              <el-radio
                :label="false"
                border
              >女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="年龄">
            <el-input v-model="userForm.age"></el-input>
          </el-form-item>
          <el-form-item label="邮箱">
            <el-input v-model="userForm.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="onExit">退出登录</el-button>
            <el-button
              type="primary"
              @click="onReviseBase"
            >修改</el-button>

          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import * as Service from "@/action/user";
export default {
  name: "PersonalBase",
  data() {
    return {
      userForm: {
        name: "",
        sex: "",
        age: "",
        email: "",
        phone: ""
      }
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.userForm = this.$store.state.user;
      // this.userForm.sex = this.userForm.sex == true ? "男" : "女";
    },
    async onReviseBase() {
      if (isNaN(Number(this.userForm.age))) {
        this.$message({
          type: "error",
          message: "年龄必须是数字哦"
        });
      } else {
        // this.userForm.sex = this.userForm == '男' ? true : false
        this.$store.dispatch("user/updateBase", this.userForm);
        try {
          await Service.reviseBase(this.userForm);
          this.$message({
            type: "success",
            message: "修改成功"
          });
        } catch (error) {
          console.log(error);
          this.$message({
            type: "error",
            message: "修改失败"
          });
        }
      }
    },
    onExit() {
      this.$confirm("此操作将退出登录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          try {
            await Service.exitLogin();
            await this.$store.dispatch('user/exitLogin')
            localStorage.removeItem('token')

            this.$message({
              type: "success",
              message: "退出成功!"
            });
            this.$router.push("/body");
          } catch (error) {
              console.log(error.response)
            this.$message({
              type: "error",
              message: "退出失败"
            });
          }
        })
        .catch(() => {});
    }
  }
};
</script>

<style scoped>
.title-text {
  font-size: 18px;
  margin-bottom: 17px;
}
.info-box {
  margin-left: 10px;
}

.head {
  padding-left: 30px;
  box-sizing: border-box;
}

.head-img {
  width: 100px;
  border-radius: 50%;
}
.form-box {
  margin-top: 20px;
  width: 50%;
}
.form-box >>> .el-radio {
  margin-right: 10px;
}
.phone {
  color: #666;
  margin-right: 17px;
}
</style>


