<template>
  <div>
    <el-menu :default-active="active" router class="nav-menu-vert">
      <div class="seize"></div>
      <el-menu-item index="/examine/myproject">
        <i class="el-icon-menu"></i>
        <span slot="title">概览</span>
      </el-menu-item>
      <el-submenu index="#1">
        <template slot="title">
          <i class="el-icon-folder-opened"></i>
          <span>项目</span>
        </template>
        <el-menu-item
          v-for="p in projectNavList"
          :key="p.key"
          :index="`/examine/projects?pid=${p.key}`"
        >
          <i class="el-icon-document-copy"></i>
          <span>{{p.name}}</span>
        </el-menu-item>
      </el-submenu>
      <el-submenu index="#2">
        <template slot="title">
          <i class="el-icon-document"></i>
          <span>模板</span>
        </template>
        <el-menu-item index="/examine/templatelib">
          <i class="el-icon-files"></i>
          <span>模板库</span>
        </el-menu-item>
        <el-menu-item index="/examine/favoritetemplate">
          <i class="el-icon-star-off"></i>
          <span>我的收藏</span>
        </el-menu-item>
      </el-submenu>
      <el-menu-item index="/examine/signature">
        <i class="el-icon-coordinate"></i>
        <span slot="title">签名管理</span>
      </el-menu-item>
      <div class="left-foot">
          <router-link to="/examine/myproject">
            <div class="foot-l">反馈</div>
          </router-link>
          <div class="vert-line"></div>
          <router-link to="/examine/myproject">
            <div class="foot-r">帮助</div>
          </router-link>
      </div>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'LeftNav',
  props: ['activeIndex'],
  computed: {
    projectNavList() {
      return this.$store.getters.projectNavList
    },
    active() {
      return this.$route.fullPath
    }
  }
}
</script>

<style scoped>
.nav-menu-vert {
  width: 100%;
  height: calc(100vh - 50px);
  overflow-y: auto;
  overflow-x: hidden;
  background-color: white;
  padding-bottom: 50px;
}
.menu-item {
  padding-left: 40px !important;
}
.seize {
  width: 100%;
  height: 80px;
}
.left-foot {
  position: fixed;
  height: 50px;
  bottom: 0px;
  font-size: 16px;
  color: #444444;
  width: calc(18% - 6px);
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}
/* .foot-in-con {
  position: relative;
} */
.foot-l {
color: #444444;
}
.foot-r {
color: #444444;
}
.vert-line {
  width: 0.1px;
  height: 30px;
  border: #666 solid 0.6px;
  /* position: absolute; */
  /* left: 0;
  right: 0;
  top: 0;
  bottom: -40px;
  margin: auto; */
}
.nav-menu-vert >>> .is-opened{
  border-right: none !important;
}
.nav-menu-vert >>> .is-active {
  border-right: #1890ff solid 3px;
}
</style>



